import template from './template.html.twig'
import './style.scss';

const { Component, Mixin } = Shopware;

export default Component.wrapComponentConfig({
    template,

    mixins: [
        Mixin.getByName('blur-style-utilities'),
    ],

    props: {
        modelValue: {
            type: String,
            required: false,
            default: '',
        },
        id: {
            type: String,
            required: true,
        },
        label: {
            type: String,
            required: false,
            default: 'Label',
        },
        placeholder: {
            type: String,
            required: false,
            default: 'Placeholder',
        },
        disabled: {
            type: Boolean,
            required: false,
            default: false,
        },
        required: {
            type: Boolean,
            required: false,
            default: false,
        },
        tooptip: {
            type: [String, undefined],
            required: false,
            default: undefined,
        },
        error: {
            type: [Object, Boolean],
            required: false,
            default: false,
        }
    },

    emits: ['update:modelValue'],
});
